import React, { Component } from 'react';
import ClaimsEditor from './ClaimsEditor';

import './css/normalize.css';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-theme.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-header navbar navbar-default">
          <div className="container">
            <div className="navbar-header">
              <a className="navbar-brand" href="#">Patent</a>
            </div>

            <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
              <ul className="nav navbar-nav">
                <li><a href="#">Claims <span className="sr-only">(current)</span></a></li>
                <li><a href="#">Specification</a></li>
              </ul>
            </div>
          </div>
        </div>

        <div className="container">
          <div className="row">
            <div className="md-row-12">
              <ClaimsEditor />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



