import CodeMirror from 'react-codemirror';
var $ = require("jquery");

(function (mod) {
	if (typeof exports === "object" && typeof module === "object") { // CommonJS
		mod(require("./../../node_modules/codemirror/lib/codemirror"));
	} else { // Plain browser env
		mod(CodeMirror);
	}
})(function (CodeMirror) {

	function matchInArray(value, arr) {
		var i = 0;
		var m = arr[i];
		while (i < arr.length && !value.match(m)) {
			m = arr[i += 1];
		}
		if (i >= arr.length) {
			return -1;
		}
		else {
			return i;
		}
	}

	var gerund = /[a-zA-Z0-9]*ing/;

	CodeMirror.defineMode("claims", function (config) {
		CodeMirror.stop_strs = null;

		CodeMirror.claim_mode = {};

		CodeMirror.stop_strs = ["ACCORDING", "ACTIVATING", "AFTER", "ALLOWS", "ALSO", "AMONG", "AND", "ARE", "ARRANGED", "AS", "ASSOCIATED", "BASED", "BECOMING", "BEFORE", "BEING", "BELONG", "BELONGS", "BETWEEN", "BROADCASTING", "BUT", "BY", "CAUSES", "CAUSING", "COMPARES", "COMPARING", "COMPRISE", "COMPRISED", "COMPRISES", "COMPRISING", "CONFIGURED", "CONFORMS", "CONNECTED", "CONNECTING", "CONTAINING", "CONTAINS", "CORRESPONDING", "CORRESPONDS", "COUNTING", "COUPLED", "DEFINED", "DEFINING", "DEFINES", "DEPENDS", "DEPRESSES", "DESIRES", "DETECTED", "DETECTS", "DETERMINES", "DISPLAYED", "DISPLAYING", "DISPLAYS", "DISPOSED", "DO", "DOES", "DURING", "EACH", "ENCODES", "ENCODING", "EXECUTE", "EXECUTES", "EXECUTING", "EXTRACTING", "FOLLOWING", "FOR", "FORWARDING", "FROM", "FURTHER", "GENERATED", "GENERATES", "GENERATES", "GENERATING", "GRANTING", "HAS", "HAVE", "HAVING", "IDENTIFIED", "IDENTIFIES", "IDENTIFYING", "IF", "ILLUSTRATES", "ILLUSTRATING", "IN", "INCLUDE", "INCLUDES", "INCLUDING", "INDICATE", "INDICATES", "INDICATING", "INDICATING", "INDICATIVE", "INITIALLY", "INTO", "IS", "INTERACTING", "LOCATED", "MADE", "MATCHES", "MAY", "OCCUR", "OCCURS", "ON", "OPERATIVELY", "OUTPUTTING", "PERFORMABLE", "PERFORMING", "PERFORMS", "PRIOR", "PROCESSES", "PROCESSING", "PROVIDES", "PROVIDING", "RECEIVE", "RECEIVING", "RELATE", "RELATING", "REPEATING", "REPRESENT", "REPRESENTATIVE", "REPRESENTED", "REPRESENTING", "REPRESENTS", "REQUESTING", "RESIDES", "RESPONSIVE", "RETRIEVES", "REVISING", "SATISFIES", "SELECTING", "SELECTIVELY", "SENDS", "SEPARATED", "SEQUENTIALLY", "SHOULD", "SPECIFIES", "SPECIFY", "SPECIFYING", "STEP", "STEPS", "STORED", "STORING", "SUCH", "TAKES", "THAN", "THAT", "THEN", "THROUGH", "TO", "TRANSFORMING", "TRANSMITS", "TRANSMITTING", "UPON", "USING", "VIOLATED", "VIOLATES", "WAS", "WHEN", "WHEREAS", "WHEREIN", "WHICH", "WILL", "WITH", "WITHIN", "WITHOUT", "of\\s+(a|an|each|one|the|said)"];

		CodeMirror.claim_mode.compile_stop_strs = function () {
			CodeMirror.claim_mode.stop_strs_re = RegExp("^(.*?)(\\b(" + CodeMirror.stop_strs.join("|") + ")\\b|:|;|,|\\.|\\s\\(|$)", 'i');
		}
		CodeMirror.claim_mode.add_or_remove_stop_str = function (cm, str) {
			// var idx = $.inArray(str.trim(), CodeMirror.stop_strs);
			var idx = CodeMirror.stop_strs.indexOf(str.trim());
			if (idx < 0) {
				CodeMirror.stop_strs.push(str.trim());
			}
			else {
				CodeMirror.stop_strs.splice(idx, 1);
			}
			CodeMirror.claim_mode.compile_stop_strs();
			var oldChar = cm.getRange({ line: 0, ch: 0 }, { line: 0, ch: 1 });
			cm.replaceRange(oldChar, { line: 0, ch: 0 }, { line: 0, ch: 1 });
		}
		CodeMirror.claim_mode.compile_stop_strs();

		function getWord(stream) {
			var a = stream.next();
			var word = a;
			while (!stream.eol() && stream.peek().match(/[a-zA-Z0-9\-\(\)\/]/)) {
				a = stream.next();
				word += a;
			}
			return word.trim();
		}

		function lookAheadFor(stream, regx, bit) {
			var m = stream.match(regx, bit);
			if (m) {
				return m[0];
			}
			return null;
		}

		function getNounPhrase(stream, bit) {
			var m = stream.match(CodeMirror.claim_mode.stop_strs_re, bit);
			var phrase = m[1];
			return phrase;
		}

		function proceedThroughNounPhrase(stream, word) {
			if (stream.peek() && !stream.peek().match(/,|\.|;|:/i)) {
				var wArr = getNounPhrase(stream, false).trim().split(' ');
				var lastWord = wArr[wArr.length - 1].trim();
				if (lastWord !== '') {
					try {
						while (!stream.eol() && !word.match(lastWord)) {
							word = getWord(stream);
						}
					} catch (err) {
						console.log("ERR:" + err);
					}
				}
			}
			return stream.current();
		}

		return {
			indent: function (state, line) {
				if (line.match(/\.\s+$/)) {
					if (line.match(/\d+\.\s.+/)) {
						state.indent = 0;
					}
				}
				else if (!state.open) {
					return 0;
				}
				//return state.indent * config.indentUnit;
				return 0;
			},
			electricChars: ":;{}",

			startState: function () {
				return {
					line: 0, indent: 0, nextIndent: 0, open: false,
					endOfBlock: false,
					syntax_errors: [],
					currClaim: 1,
					claims: {
						1: {
							def_np: [],
							def_np_strs: [],
							depends_on: null
						}
					},
					claim_count: 0
				};
			},

			copyState: function (state) {
				// return JSON.parse(JSON.stringify(state)); // this does not work as deep copy, not sure why though.
				return $.extend(true, {}, state); // do a recursive deep copy
			},

			blankLine: function (state) {
				state.line += 1;
			},

			token: function (stream, state) {
				if (stream.sol()) state.indented = stream.indentation();

				// Look for beginning of new claim
				var claim_num = stream.string.match(/^(\d+)\./);
				if (claim_num && !state.open) {
					claim_num = claim_num[1];
					state.open = true;
					state.currClaim = claim_num;
					if (typeof (state.claims[state.currClaim]) === 'undefined') {
						state.claims[state.currClaim] = {
							def_np: [],
							def_np_strs: [],
							depends_on: null
						};
					}

					var match = stream.string.match(/claim (\d+)/i);
					if (match) {
						var prevClaim = match[1];
						var claim_regex = RegExp('.* of claim ' + prevClaim, 'i');
						state.claims[state.currClaim].depends_on = prevClaim;

						state.claims[state.currClaim].def_np = state.claims[prevClaim].def_np.slice();
						state.claims[state.currClaim].def_np_strs = state.claims[prevClaim].def_np_strs.slice();

						state.claims[state.currClaim].def_np.push(claim_regex);
						state.claims[state.currClaim].def_np_strs.push(claim_regex.toString());
					}
				}

				// update indenting
				if (stream.sol()) {
					state.indent = state.nextIndent;
					if (state.endOfBlock) {
						state.nextIndent -= 1;
						state.endOfBlock = false;
					}
				}
				var w = getWord(stream);
				if (stream.eol()) {
					state.line += 1;
					var line = stream.string;
					var lastChar = line[line.length - 1];
					switch (lastChar) {
						case ':':
							state.nextIndent += 1;
							break;
						case '{':
							state.nextIndent += 1;
							break;
						case '}':
							state.nextIndent -= 1;
							break;
						default:

					}

					if (line.match(/\.\s*$/)) {
						state.nextIndent = 0;
						state.open = false;
					}
					else if (line.match(/;\s*and\s*$/)) {
						state.endOfBlock = true;
					}
				}

				// Look for noun phrases and uses of noun phrases
				if (w.match(/\/{2}/)) {
					return 'claim-comment';
				}
				else if (w.match(/(^|\s)(a|an|each)\b/i) ||
					(w.match(/one/) && lookAheadFor(stream, /\s?or\smore/, false)) ||
					(w.match(/at/) && lookAheadFor(stream, /\s+least(?!\sin\spart)/, false))) {
					var idx = 0, np = null, np_re = null;
					if (w.match(/\b(a|an|each)\b/i)) {
						idx = w.length + 1;
					}
					else if (w.match(/\b(one|at)\b/) && lookAheadFor(stream, /\s?or more\b|\s? least/, false)) {
						idx = 0;
					}
					np = proceedThroughNounPhrase(stream, w).trim();
					np_re = RegExp('^' + np.substring(idx) + '$', 'i');

					// check for the same entry twice
					// if ($.inArray(np_re.toString(), state.claims[state.currClaim].def_np_strs) < 0) {
					if (state.claims[state.currClaim].def_np_strs.indexOf(np_re.toString()) < 0) {
						state.claims[state.currClaim].def_np.push(np_re);
						state.claims[state.currClaim].def_np_strs.push(np_re.toString());
						return 'claim-noun-init';
					}
					else if (w.match(/\beach\b/)) { // make exception for "each"
						return 'claim-noun-init';
					}
					else {
						state.syntax_errors.push([np, state.line]);
						return 'claim-noun-init-error';
					}
				}
				else if (w.match(/\b(the|said)\b/i)) {
					idx = w.length + 1;
					np = proceedThroughNounPhrase(stream, w).trim().substring(idx);
					idx = matchInArray(np, state.claims[state.currClaim].def_np);
					if (idx < 0) {
						state.syntax_errors.push([np, state.line]);
						return 'claim-noun-use-error';
					}
					else {
						return 'claim-noun-use';
					}
				}
				else if (w.match(gerund)) { // ing words
					return 'claim-gerund';
				}
				else if (w === ':' || w === ';' || w === '.') {
					return 'line-claim-indent-' + state.indent; // the 'line' in 'line-claim-indent' is removed when rendered
				}
				else {
					return null;
				}
			}
		};
	});

	CodeMirror.defineMIME("text/x-claim", "claim");
});


