import React, { Component } from 'react';
import './ClaimsEditor.css';
import './../node_modules/codemirror/lib/codemirror.css';
import './../node_modules/codemirror/mode/javascript/javascript';
import CodeMirror from 'react-codemirror';

import './claims/claims.css';
import './claims/claims.js';

class ClaimsEditor extends Component {

  constructor(props) {
    super(props);

    var initCode = '';
    
    if(process.env.NODE_ENV === 'development') {
      initCode = `1. A method comprising:
doing a first thing;
doing a second thing;
doing a third thing with the first thing and the second thing;
doing a first thing with the fifth thing.

2.	The method of Claim 1, further comprising:
doing a fifth thing;
doing the first thing with the fifth thing.

3.The method of Claim 1, futher comprising:
doing the first thing with the fifth thing.`; 
    } 

    this.state = {
      code: initCode
    };
  }
    

  updateCode = (newCode) => {
    this.setState({
      code: newCode,
    });
  }

  render() {
    var options = {
      lineNumbers: true,
      mode: 'claims'
    };

    return <CodeMirror className="claims-editor" value={this.state.code} onChange={this.updateCode} options={options} />
  }
}

export default ClaimsEditor;


